
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Masker from 'vux/src/components/masker/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
components: {
ViewBox,
XHeader,
Masker,
Tab,
TabItem,
Group,
XInput,
XSwitch,
Cell
},
data () {
return {
index: 0,
isChangePwd: true,

oldPwd: '',
newPwd: '',
reNewPwd: '',
gesturePwd: ''
}
},
mounted () {
},
watch: {
isOpenGesture (newval) {
if (newval) {
this.openGesture()
} else {
this.closeGesture()
}
}
},
computed: {
isOpenGesture: {
set (val) {
this.$store.state.isOpenGesture = val
},
get () {
return this.$store.state.isOpenGesture
}
}
},
methods: {
handler (flag) {
if (flag === 0) {
this.isChangePwd = true
} else {
this.isChangePwd = false
}
},
changePwd () {
let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/resetPassword.do'
let data = {
action: 'changephone_request',
path: path,
params: {
userToken: this.$store.state.userInfo.token,
oldpass: this.oldPwd,
newpass: this.newPwd
}
}
if (self.mathchPwd(self.oldPwd)) {
if (self.mathchPwd(self.newPwd)) {
if (self.newPwd === self.reNewPwd) {
this.$vux.loading.show({
text: 'Loading'
})

self.$store.dispatch('normalRequest', data).then(response => {
if (response.response === 'success') {
this.$vux.toast.text('')
self.$store.state.userInfo.token = ''
self.$store.state.userInfo.cellphone = ''
this.$router.replace('/login')
} else {
this.$vux.toast.text(response.data)
}
this.$vux.loading.hide()
})
} else {
this.$vux.toast.text('')
}
}



}



},
updataGesture () {

let self = this
let data = {
action: 'jump_ges_update'
}
self.$store.dispatch('normalRequest', data).then(res => {
this.$vux.toast.text('')
})
},
openGesture () {

let self = this
let data = {
action: 'jump_ges_lock'
}
self.$store.dispatch('normalRequest', data).then(data => {
if (data.isOpenGesture) {
this.$store.state.isOpenGesture = true
} else {
this.$store.state.isOpenGesture = false
}
})
},
closeGesture () {

let self = this
let data = {
action: 'jump_ges_close'
}
self.$store.dispatch('normalRequest', data).then(res => {
this.$vux.toast.text('')
})
}
}
}



